import {
	mapState
} from 'vuex'
import {
	toMoney
} from '@/vendor/filters.js'
import {
	getTasks
} from '@/api/index.js'

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	watch: {
		selectedType(newValue, oldValue) {
			uni.pageScrollTo({
				scrollTop:0
			})
			uni.startPullDownRefresh()
			
		}
	},
	data() {
		return {
			items: [],
			types: [{
					text: '全部'
				},
				{
					text: '已完成'
				},
				{
					text: '待支付'
				},
				{
					text: '执行中'
				},
				{
					text: '待接单'
				},
			],
			selectedType: 0,

			page: 0,
			pageSize: 20
		}
	},
	onShow() {
		this.getItems()
	},

	onPullDownRefresh() {
		this.getItems().then(() => {
			uni.stopPullDownRefresh();
		}).catch(() => {
			uni.stopPullDownRefresh();
		});
	},
	methods: {
		async getItems() {
			this.$$.showLoading();
			try {
				const items = await getTasks(this.page, this.pageSize);
				this.items = items.data;
				this.page++;
			} catch (e) {
				console.error(e);
				this.$$.showErr();
			}
			this.$$.hideLoading();
		},
		openDetail(id) {
			uni.navigateTo({
				url: `/pages/detail/detail?id=${id}`
			});
		}
	},
	filters: {
		toMoney: toMoney
	},
}
